import React from "react";
import { useLocation, Link } from "react-router-dom";
import "./VoiceInteractionPage.css";

const VoiceInteractionPage = () => {
  const location = useLocation();
  const transcript = location.state?.transcript || "No speech detected.";

  return (
    <div className="voice-interaction-page">
      <h1>Amazing Voice Interaction!</h1>
      <p className="transcript-text">You said:</p>
      <div className="speech-box">{transcript}</div>
      <Link to="/" className="back-btn">Go Back Home</Link>
    </div>
  );
};

export default VoiceInteractionPage;
